// src/config/adminAccess.js
// Configurao especial para garantir acesso total de administradores

/**
 * Configurao de acesso administrativo
 * ADMINS tm poder MXIMO e podem fazer TUDO no sistema
 */
export const ADMIN_ACCESS_CONFIG = {
    // Lista de usurios que sempre tm acesso total (alm dos admins)
    SUPER_USERS: [
        'admin', 
        'administrador',
        'root',
        'super_admin'
    ],
    
    // Rotas que SEMPRE esto disponveis para admins
    ADMIN_ALWAYS_ALLOWED_ROUTES: [
        '/cadastro-familia',
        '/cadastro-monitor',
        '/lista-familias', 
        '/lista-monitores',
        '/editar-familia',
        // Consulta geral removida
        '/admin',
        '/configuracoes',
        '/usuarios',
        '/relatorios'
    ],

    // Aes que admins SEMPRE podem fazer
    ADMIN_ALWAYS_ALLOWED_ACTIONS: [
        'read',
        'write', 
        'update',
        'delete',
        'create',
        'manage',
        'configure',
        'admin'
    ],

    // Recursos que admins SEMPRE podem gerenciar
    ADMIN_ALWAYS_ALLOWED_RESOURCES: [
        'familias',
        'monitores', 
        'usuarios',
        'entrevistas',
        'cadastros',
        'relatorios',
        'configuracoes',
        'sistema',
        'dados'
    ]
};

/**
 * Verifica se um usurio  um super admin com acesso total
 * @param {Object} user - Dados do usurio
 * @returns {boolean} - true se  super admin
 */
export const isSuperAdmin = (user) => {
    if (!user) return false;
    
    // Verifica por role
    const role = (user.role || user.tipo_usuario || '').toLowerCase();
    if (role === 'admin' || role === 'administrador') {
        return true;
    }
    
    // Verifica por username especfico
    const username = (user.username || user.nome_usuario || user.email || '').toLowerCase();
    return ADMIN_ACCESS_CONFIG.SUPER_USERS.some(superUser => 
        username.includes(superUser.toLowerCase())
    );
};

/**
 * Fora o acesso para administradores - BYPASS completo de permisses
 * @param {Object} user - Dados do usurio
 * @param {string} action - Ao desejada
 * @param {string} resource - Recurso alvo
 * @returns {boolean} - true se deve permitir acesso
 */
export const forceAdminAccess = (user, action, resource) => {
    // Se  super admin, SEMPRE permite
    if (isSuperAdmin(user)) {
        console.log(` ADMIN ACCESS: Usurio ${user.nome_usuario || user.email} tem acesso TOTAL como administrador`);
        return true;
    }
    
    return false;
};

/**
 * Verifica se uma rota  sempre permitida para admins
 * @param {Object} user - Dados do usurio  
 * @param {string} route - Rota desejada
 * @returns {boolean} - true se deve permitir
 */
export const forceAdminRouteAccess = (user, route) => {
    if (isSuperAdmin(user)) {
        // Admin pode acessar QUALQUER rota
        console.log(` ADMIN ROUTE ACCESS: Administrador ${user.nome_usuario || user.email} pode acessar rota: ${route}`);
        return true;
    }
    
    return false;
};

/**
 * Middleware de autenticao que garante acesso administrativo
 * @param {Object} user - Dados do usurio
 * @param {string} action - Ao tentada  
 * @param {string} resource - Recurso alvo
 * @param {Function} normalCheck - Funo de verificao normal de permisso
 * @returns {boolean} - Resultado final da verificao
 */
export const checkAccessWithAdminOverride = (user, action, resource, normalCheck) => {
    // PRIMEIRO: Verifica se  admin com acesso forado
    if (forceAdminAccess(user, action, resource)) {
        return true;
    }
    
    // SEGUNDO: Se no  admin, usa verificao normal
    return normalCheck ? normalCheck() : false;
};

/**
 * Helper para exibir mensagens de acesso administrativo
 * @param {Object} user - Dados do usurio
 * @returns {string} - Mensagem apropriada
 */
export const getAdminAccessMessage = (user) => {
    if (isSuperAdmin(user)) {
        return ` Acesso TOTAL de Administrador confirmado para: ${user.nome_usuario || user.email}`;
    }
    
    const role = user?.role || user?.tipo_usuario || 'Usurio';
    return ` Acesso baseado no perfil: ${role}`;
};

export default {
    ADMIN_ACCESS_CONFIG,
    isSuperAdmin,
    forceAdminAccess,
    forceAdminRouteAccess,
    checkAccessWithAdminOverride,
    getAdminAccessMessage
};