import React from 'react';
import { useAuth } from '../hooks/useAuth';

const ProtectedRoute = ({ children, requiredRoles = null, fallback = null }) => {
    const { isAuthenticated, hasPermission, isLoading } = useAuth();

    // Mostrar loading enquanto verifica autenticao
    if (isLoading) {
        return (
            <div style={{ 
                display: 'flex', 
                justifyContent: 'center', 
                alignItems: 'center', 
                height: '100vh' 
            }}>
                <div>Carregando...</div>
            </div>
        );
    }

    // Se no est autenticado, no renderizar nada (ser redirecionado)
    if (!isAuthenticated) {
        return null;
    }

    // Se requer roles especficos, verificar permisso
    if (requiredRoles && !hasPermission(requiredRoles)) {
        return fallback || (
            <div style={{ 
                padding: '20px', 
                textAlign: 'center', 
                color: '#d33' 
            }}>
                <h3>Acesso Negado</h3>
                <p>Voc no tem permisso para acessar esta pgina.</p>
            </div>
        );
    }

    return children;
};

export default ProtectedRoute;