// src/utils/permissions.js
// Utilitrios para verificao de permisses de usurios

import { forceAdminAccess, forceAdminRouteAccess, isSuperAdmin } from '../config/adminAccess';

/**
 * Verifica se o usurio tem permisso para realizar uma ao especfica
 * @param {Object} user - Objeto do usurio com role e outras informaes
 * @param {string} action - Ao que o usurio quer realizar
 * @param {string} resource - Recurso sobre o qual a ao ser realizada
 * @returns {boolean} - true se permitido, false caso contrrio
 */
export const hasPermission = (user, action, resource = null) => {
    if (!user || !user.role) {
        return false;
    }

    //  PRIMEIRO: Verifica se  admin com acesso forado (BYPASS TOTAL)
    if (forceAdminAccess(user, action, resource)) {
        return true;
    }

    const userRole = (user.role || user.tipo_usuario || '').toLowerCase();

    // ADMIN tem poder MXIMO - pode fazer TUDO no sistema
    if (userRole === 'admin' || userRole === 'administrador') {
        console.log(` ADMIN PERMISSION: ${user.nome_usuario || user.email} tem acesso TOTAL como administrador`);
        return true;
    }

    // Define as permisses por role
    const permissions = {
        coordenador: {
            // Coordenador pode fazer quase tudo, exceto gerenciar usurios
            read: ['familias', 'monitores', 'relatorios', 'entrevistas', 'cadastros'],
            write: ['familias', 'monitores', 'entrevistas', 'cadastros'],
            update: ['familias', 'monitores', 'entrevistas', 'cadastros'],
            delete: ['familias', 'monitores', 'entrevistas']
        },
        monitor: {
            // Monitor pode cadastrar e editar famlias, ver listagens
            read: ['familias', 'monitores', 'relatorios'],
            write: ['familias', 'entrevistas'],
            update: ['familias', 'entrevistas'],
            delete: [] // Monitor no pode deletar
        },
        visualizador: {
            // Visualizador s pode ver dados
            read: ['familias', 'monitores', 'relatorios'],
            write: [],
            update: [],
            delete: []
        }
    };

    const userPermissions = permissions[userRole];
    if (!userPermissions) {
        return false;
    }

    // Verifica se o usurio tem permisso para a ao especfica
    if (userPermissions[action] && userPermissions[action].includes(resource)) {
        return true;
    }

    return false;
};

/**
 * Verifica se o usurio pode acessar uma rota especfica
 * @param {Object} user - Objeto do usurio
 * @param {string} route - Rota que o usurio quer acessar
 * @returns {boolean} - true se permitido, false caso contrrio
 */
export const canAccessRoute = (user, route) => {
    if (!user) {
        return false;
    }

    //  PRIMEIRO: Verifica se  admin com acesso forado (BYPASS TOTAL)
    if (forceAdminRouteAccess(user, route)) {
        return true;
    }

    const userRole = (user.role || user.tipo_usuario || '').toLowerCase();

    // ADMIN pode acessar TODAS as rotas
    if (userRole === 'admin' || userRole === 'administrador') {
        console.log(` ADMIN ROUTE: ${user.nome_usuario || user.email} pode acessar QUALQUER rota como administrador`);
        return true;
    }

    // Define as rotas permitidas por role
    const routePermissions = {
        coordenador: [
            '/cadastro-familia',
            '/cadastro-monitor', 
            '/lista-familias',
            '/lista-monitores',
            '/editar-familia'
        ],
        monitor: [
            '/cadastro-familia',
            '/lista-familias',
            '/lista-monitores',
            '/editar-familia'
        ],
        visualizador: [
            '/lista-familias',
            '/lista-monitores'
        ]
    };

    const allowedRoutes = routePermissions[userRole] || [];
    return allowedRoutes.some(allowedRoute => route.startsWith(allowedRoute));
};

/**
 * Retorna uma mensagem de erro personalizada baseada na permisso negada
 * @param {Object} user - Objeto do usurio
 * @param {string} action - Ao negada
 * @param {string} resource - Recurso relacionado
 * @returns {string} - Mensagem de erro
 */
export const getPermissionErrorMessage = (user, action, resource) => {
    const userRole = user?.role || 'usurio no autenticado';
    
    const actionMessages = {
        'delete': `Seu perfil (${userRole}) no tem permisso para deletar ${resource}. Apenas administradores e coordenadores podem realizar esta ao.`,
        'write': `Seu perfil (${userRole}) no tem permisso para criar ${resource}. Contate um administrador.`,
        'update': `Seu perfil (${userRole}) no tem permisso para editar ${resource}. Contate um administrador.`,
        'read': `Seu perfil (${userRole}) no tem permisso para visualizar ${resource}.`
    };

    return actionMessages[action] || `Permisso negada para a ao '${action}' no recurso '${resource}'.`;
};

/**
 * Utilitrio para criar componentes protegidos por permisso
 * @param {Function} Component - Componente a ser renderizado
 * @param {string} requiredPermission - Permisso necessria
 * @param {string} resource - Recurso relacionado
 * @returns {Function} - Componente protegido
 */
export const withPermission = (Component, action, resource) => {
    return function ProtectedComponent(props) {
        const user = props.user || (props.auth && props.auth.user);
        
        if (!hasPermission(user, action, resource)) {
            return (
                <div style={{
                    padding: '20px',
                    textAlign: 'center',
                    background: '#f8d7da',
                    border: '1px solid #f5c6cb',
                    borderRadius: '8px',
                    color: '#721c24'
                }}>
                    <h3> Acesso Negado</h3>
                    <p>{getPermissionErrorMessage(user, action, resource)}</p>
                    <p><strong>Seu perfil:</strong> {user?.role || 'No autenticado'}</p>
                </div>
            );
        }

        return <Component {...props} />;
    };
};

export default {
    hasPermission,
    canAccessRoute,
    getPermissionErrorMessage,
    withPermission
};